___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  color: white;
}

button {
  background-color: #635215;
  border: none;
  border-radius: 5px;
  padding: 0.5rem;
  cursor: pointer;
}

button:hover {
  background-color: #a79e23;
}

.container {
  display: flex;
  height: 100vh;
}

.sidebar {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background-color: #152663;
  padding: 1rem;
  width: 20vw;
  text-align: center;
}

.main-content {
  background-color: #213679;
  padding: 1rem;
  width: 80vw;
}

.main-title-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-bottom: 1rem;
}

/* Sidebar Projects */
.projects {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.projects-header {
  display: flex;
  justify-content: center;
}

.projects-header button {
  margin-left: 2rem;
  padding: 0 1rem;
}

.projectGrid {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}

.project {
  overflow: hidden;
  text-overflow: ellipsis;
  grid-column: 1 / span 5;
}

.editProjBtn {
  grid-column: 7
}

/* Modals */
.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.modal-content {
  background-color: #152663;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 30%;
}

input,
textarea,
select,
option {
  color: black;
}

.modal-content p {
  padding: 1rem;
  gap: 0.5rem;
  display: flex;
  flex-direction: column;
}

#closeAddProjModal,
#closeEditProjModal,
#closeAddTaskModalSpan,
#closeEditTaskModalSpan {
  color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

#closeAddProjModal:hover,
#closeAddProjModal:focus,
#closeEditProjModal:hover,
#closeEditProjModal:focus,
#closeAddTaskModalSpan:hover,
#closeAddTaskModalSpan:focus,
#closeEditTaskModalSpan:hover,
#closeEditTaskModalSpan:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

/* Tasks */
.tasksList {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.taskContainer {
  display: flex;
}

.taskInfoContainer:hover {
  opacity: 0.8 !important;
  cursor: pointer !important;
}

.taskBtnContainer {
  display: flex;
  gap: 1rem;
}

.taskInfoContainer {
  display: flex;
  width: 90%;
  justify-content: space-between;
  padding: 0 1rem;
  margin-right: 1rem;
  align-items: center;
  background-color: #262626;
}

.taskDescDiv {
  background-color: #262626;
  width: 85%;
  margin: 0 1rem;
  padding: 0.4rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;EACE,SAAS;EACT,UAAU;EACV,YAAY;AACd;;AAEA;EACE,yBAAyB;EACzB,YAAY;EACZ,kBAAkB;EAClB,eAAe;EACf,eAAe;AACjB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,aAAa;AACf;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,yBAAyB;EACzB,aAAa;EACb,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,yBAAyB;EACzB,aAAa;EACb,WAAW;AACb;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,SAAS;EACT,mBAAmB;AACrB;;AAEA,qBAAqB;AACrB;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,aAAa;EACb,uBAAuB;AACzB;;AAEA;EACE,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,qCAAqC;AACvC;;AAEA;EACE,gBAAgB;EAChB,uBAAuB;EACvB,uBAAuB;AACzB;;AAEA;EACE;AACF;;AAEA,WAAW;AACX;EACE,aAAa;EACb,eAAe;EACf,UAAU;EACV,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,cAAc;AAChB;;AAEA;EACE,yBAAyB;EACzB,gBAAgB;EAChB,aAAa;EACb,sBAAsB;EACtB,UAAU;AACZ;;AAEA;;;;EAIE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,WAAW;EACX,aAAa;EACb,sBAAsB;AACxB;;AAEA;;;;EAIE,YAAY;EACZ,YAAY;EACZ,eAAe;EACf,iBAAiB;AACnB;;AAEA;;;;;;;;EAQE,YAAY;EACZ,qBAAqB;EACrB,eAAe;AACjB;;AAEA,UAAU;AACV;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,uBAAuB;EACvB,0BAA0B;AAC5B;;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,aAAa;EACb,UAAU;EACV,8BAA8B;EAC9B,eAAe;EACf,kBAAkB;EAClB,mBAAmB;EACnB,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;EACzB,UAAU;EACV,cAAc;EACd,eAAe;AACjB","sourcesContent":["* {\r\n  margin: 0;\r\n  padding: 0;\r\n  color: white;\r\n}\r\n\r\nbutton {\r\n  background-color: #635215;\r\n  border: none;\r\n  border-radius: 5px;\r\n  padding: 0.5rem;\r\n  cursor: pointer;\r\n}\r\n\r\nbutton:hover {\r\n  background-color: #a79e23;\r\n}\r\n\r\n.container {\r\n  display: flex;\r\n  height: 100vh;\r\n}\r\n\r\n.sidebar {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 1rem;\r\n  background-color: #152663;\r\n  padding: 1rem;\r\n  width: 20vw;\r\n  text-align: center;\r\n}\r\n\r\n.main-content {\r\n  background-color: #213679;\r\n  padding: 1rem;\r\n  width: 80vw;\r\n}\r\n\r\n.main-title-buttons {\r\n  display: flex;\r\n  justi